ul{
    list-style-type: none;
    margin: 0;
  padding: 0;
  overflow: hidden;
  background-color:rgb(0,0,0);
  
  
  }
  
  li{
    float: left;
  
  }

  
  li a {
    display: block;
    color: rgb(253, 253, 253);
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    text-shadow: 1px 1px 2px black, 0 0 25px blue, 0 0 5px darkblue;
  }
  
  li a:hover:not(.active) {
    background-color: #111;
  }