

  .bioPage{
    background: url('../Images/IMG_4987.jpg');
    min-height: 100vh;
    background-position: center center;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover; 
  background-repeat: no-repeat;
      
  display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
  
  }

  h2{
    color:rgb(120, 44, 57);
  }
  p{
    font-weight: 700px;
    color:rgb(154, 154, 157);
    /* background-color: black; */
    border-radius: 30px;
    opacity: .9 ;
  }
  .Front-pic {
    height: 50vmin;
    pointer-events: none;
    
    border-radius: 45px;
  }

.RButton{
  
  display: block;
  height: 75px;
  width: 250px;
  font-size: 30px;
color: #ffffff;
background-color: rgb(0, 0, 0);
border-radius: 10px;
border-color: rgb(0, 0, 0);
opacity: .7;
transition-duration: 0.4s;
outline:none;
padding: 10px;
font-weight: bold;
text-align: center; 
}
.RButton:hover{
    background-color: rgb(248, 248, 248);
    color: rgb(2, 2, 2);
    opacity: .8;
  }

  .paragraph{
    background-color: black;
    text-align:center;  
    font-weight: bold;
    align-content: center;
    box-shadow: 0 4px 8px 0 rgba(173, 148, 5, 0.2);
    max-width: 900px;
    margin: auto;
    text-align: center;
    font-family: arial;
    border-radius: 25px;
    padding: 5px;
    opacity: .9;
  }

 